import React from 'react';
import ImageCarousel from '../Components/ImageCarousel';

const StaticProject = ({images, description}) => {
    return (
        <div className="static-project">
            <ImageCarousel images={images} />
            <div style={{padding: '0px 10px 10px 10px', textAlign: 'left'}}>
                <p>{description}</p>
            </div>
        </div>
    )
}

export default StaticProject;