import React, {useState} from "react";

const ImageCarousel = ({images}) => {
    const [index, setIndex] = useState(0);

    const addIndex = () => {
        if (index === images.length - 1) {
            setIndex(0)
        } else {
            const newIndex = index + 1;
            setIndex(newIndex)
        }
    }

    const subtractIndex = () => {
        if (index === 0) {
            const lastImage = images.length-1;
            setIndex(lastImage)
        } else {
            const newIndex = index - 1;
            setIndex(newIndex)
        }
    }
    return (
        <div id="image-carousel">
            <div id="image-carousel-container">
                <img src={images.length ? images[index] : ""} alt="None" />
            </div>
            <div id="button-container">
                <button onClick={subtractIndex}><ion-icon name="arrow-back-outline"></ion-icon></button>
                <button onClick={addIndex}><ion-icon name="arrow-forward-outline"></ion-icon></button>
            </div>
        </div>
    )
}

export default ImageCarousel
