import React, { useEffect} from 'react';
import main from '../static/images/main.jpg'
import ContactForm from '../Components/ContactForm';
import CustomerReview from '../Components/CustomerReview';
import data from '../test';
import './home.css';



const Home = (props) => {
    useEffect(() => {
        setTimeout(function() {
            document.getElementById("top-image").style.opacity = 1;
            //document.getElementById("main-image").style.opacity = 1;
            document.getElementById("main-image-text").style.opacity = 1;
        }, 1000)
    }, [])

    return (
        <React.Fragment>
           <div id="top-container">
               <div id="top-image">
                    {/* <img id="main-image" src={main} alt="Main" /> */}
                    <div id="main-image-text">
                        Custom Designed<br /> Furniture
                    </div>
               </div>
               <hr style={{width: '75%', height: '30px', backgroundColor: '#707070'}}/>
            </div>
            <div id="reviews-section">
                <h3 id="top-container-header">Customer Reviews</h3>
                <div id="reviews-container">
                    {data.Reviews.map(item => {
                        return <CustomerReview key={item.id} review={item.review} date={item.date} />
                    })}
                </div>
                <div style={{marginTop: '50px'}}>
                    <p style={{marginBottom: '0px'}}>To read more reviews,</p>
                    <p style={{marginTop: '0px'}}>
                        visit our 
                        <a href="https://www.instagram.com/6to1designs/" target="_blank"> Facebook </a> 
                        or
                        <a href="https://www.facebook.com/pg/6to1designs" target="_blank"> Instagram </a>
                        pages.</p>
                    <div id="review-circles">
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                </div>
            </div>
            <div id="text-box">
                <div>
                    <p>Know what you want?</p>
                    <p>We'll create it.</p>
                </div>
            </div>
            <div id="contact-container" className="container-fluid">
                <h1 id="contact-container-header">Start Your Project Today</h1>
                <ContactForm />
            </div>
            </React.Fragment>
    )
}

export default Home;